.header__burger {
  display: none;
  padding: 13px;
  background: linear-gradient(135deg, rgba(69, 252, 219, 0.8) 0%, rgba(30, 132, 252, 0.8) 96.83%), linear-gradient(135deg, #A1FF8B 0%, #3F93FF 96.83%), #3DAAD4;
  border-radius: 5px;
